___CSS_LOADER_EXPORT___.push([module.id, `#number{
    background-color:lightslategray;
}

#clear{
    background-color:lightcoral;
}

#calc{
    margin-left:auto;
    margin-right:auto;
    size: 150px;
    background-color:pink;
    border-radius:10px;
    border-style:solid;
    padding:30px;
    padding-top:10px;
    display:grid;
    grid-template-columns:repeat(4, 1fr);
    grid-template-rows: repeat(5, 1fr);
}

#calc {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#result, #clear {
    width: 100%;
    margin-bottom: 10px;
}

#buttons {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(5, 1fr);
    gap: 10px;
    width: 100%;
}

#symbol{
    background-color:lightskyblue;
}

#equal{
    background-color:lightcoral;
    padding-right:175px;
}

input{
    border-radius:25px;
    padding:30px;
    font-size:X-large;
}

#number:hover{
    background-color:white;
    cursor:pointer;
}

#symbol:hover{
    background-color:white;
    cursor:pointer;
}

#equal:hover{
    background-color:crimson;
    cursor:pointer;
}

#clear:hover{
    background-color:crimson;
    cursor:pointer;
}
